import React, { useEffect, useState, useTransition } from "react";
import axios from "axios";
import "../styles/PublicPages.css";
import { Checkbox, Radio, message } from "antd";
import { useNavigate } from "react-router-dom";

const CrudPage = () => {
  let count = 0;
  const navigate = useNavigate();
  const [users, setUsers] = useState([]);
  const [radio, setRadio] = useState([]);
  const getAllDemoUsers = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/user/fetchall`,
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        message.success("users list fetch successfully");
        setUsers(res?.data);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleDelete = async (value) => {
    try {
      const res = await axios.delete(
        `${process.env.REACT_APP_API}/api/v1/user/deleteduser/${value}`,
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        message.success("delete successful");
        window.location.reload();
        navigate("/crudpage");
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getAllDemoUsers();
  }, [radio]);

 
  return (
    <>
      {/* <h1>{radio}</h1> */}
      <div className="crud " style={{ height: "85vh" }}>
        <div className="left-t">
          <button
            className="btn btn-primary"
            onClick={() => {
              navigate("/createdemouser");
            }}
          >
            create new user
          </button>
        </div>
        <table className="table">
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">Name</th>
              <th scope="col">Address</th>
              <th scope="col">Age</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {users?.data?.map((u) => (
              <tr>
                <td>{(count = count + 1)}</td>
                <td>{u.name}</td>
                <td>{u.address}</td>
                <td>{u.age}</td>
                <td>
                  {
                    <button
                      className="btn btn-info"
                      onClick={() => {
                        navigate(`/updatepage/${u._id}`);
                      }}
                    >
                      Update
                    </button>
                  }
                </td>
                <td>
                  {
                    <button
                      className="btn btn-danger"
                      onClick={() => handleDelete(u._id)}
                    >
                      Delete
                    </button>
                  }
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </>
  );
};

export default CrudPage;
